h1{
    /* margin-left:500px; */
    color: hwb(300 4% 45%);
}
.inputform
{
    background-color: rgb(138, 222, 135);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    border-radius: 10px;
    padding: 30px 20px;
    width: 500px;
  
}
.inputform input,textarea{
    padding: 10px 20px;
    width: 350px;
    margin: 10px;
}
.inputform button{
    padding: 10px 20px;
    border-radius: 5px;
    font-size: 16px;
    background-color:hwb(300 2% 62%);;
    color: whitesmoke;
    border: none;
    font-weight: 300;
}
